  .note{
    background-color: #62bbff;
    
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;
  }
  
  .notes-list {
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(
      auto-fill,
      minmax(250px, 1fr)
    );
  }
  
  .note-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .note.new{
    background-color: #ffe30d;
  
  }
  
  textarea{
    border: none;
    resize: none;
    background-color: #ffe30d;
  
  }
  
  textarea:focus{
    outline: none;
  }
  
  .save{
    background-color: #e1e1e1;
    border:none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
  }
  
  .save:hover{
    background-color: #ededed;
    cursor:pointer;
  }
  
  .delete-icon{
    cursor: pointer;
  }
  
  .search{
    display:flex;
    align-items: center;
    background-color: rgb(233,233,233);
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 1.5em;
  
  }
  
  .search input{
    border: none;
    background-color: rgb(233,233,233);
  
  }
  
  .search input:focus{
    outline:none;
  
  }
  
  .header{
    display:flex;
    align-items: center;
    justify-content: space-between;
  
  }

  /* date-match timer starts */
  
  .dark-mode{
    background-color: black;
  }
  
  .dark-mode h1{
    color: white;
  }
  
  .container{
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 15px;
    padding-left: 15px;
    min-height: 100vh;
  }